 (University of Warwick) "),_c('br'),_vm._v("16.Helen (Hai) Li (Duke University)"),_c('br'),_vm._v(" 17.Jalaj Upadhyay (Apple) "),_c('br'),_vm._v("18.Ji Feng (Sinnovation Ventures AI Institute) "),_c('br'),_vm._v("19.Jian Liu (Ping An Asset Management) "),_c('br'),_vm._v("20.Jianshu Weng (Swiss Re) "),_c('br'),_vm._v("21.Jihong Park (University of Oulu) "),_c('br'),_vm._v("22.Joshua Gardner (University of Michigan) "),_c('br'),_vm._v("23.Jun Zhao (Nanyang Technological University) "),_c('br'),_vm._v("24.Junyang Li (E Funds) ")])]),_c('div',{staticClass:"item-box-right"},[_c('div',[_vm._v(" 25.Lalitha Sankar (Arizona State University) "),_c('br'),_vm._v("26.Leye Wang (Peking University) "),_c('br'),_vm._v("27.Marco Gruteser (Google)"),_c('br'),_vm._v(" 28.Martin Jaggi (Ecole Polytechnique Fédérale de Lausanne) "),_c('br'),_vm._v("29.Mehdi Bennis (University of Oulu) "),_c('br'),_vm._v("30.Mingshu Cong (The University of Hong Kong) "),_c('br'),_vm._v("31.Nguyen Tran (The University of Sydney) "),_c('br'),_vm._v("32.Ning Cheng (E Fuds)"),_c('br'),_vm._v(" 33.Pingzhong Tang (Tsinghua University) "),_c('br'),_vm._v("34.Praneeth Vepakomma (MIT) "),_c('br'),_vm._v("35.Prateek Mittal (Princeton University)"),_c('br'),_vm._v(" 36.Rui Lin (Chalmers University of Technology) "),_c('br'),_vm._v("37.Sewoong Oh (University of Illinois at Urbana-Champaign)"),_c('br'),_vm._v(" 38.Shiqiang Wang (IBM) "),_c('br'),_vm._v("39.Siwei Feng (Nanyang Technological University) "),_c('br'),_vm._v("40.Tara Javidi (University of California San Diego) "),_c('br'),_vm._v("41.Xiaoyu Wang (E Funds) "),_c('br'),_vm._v("42.Yihan Jiang (University of Washington) "),_c('br'),_vm._v("43.Yongxin Tong (Beihang University) "),_c('br'),_vm._v("44.Tongzhe Zhang (E Funds) "),_c('br'),_vm._v("45.Yuxiang Wang (E Funds) "),_c('br'),_vm._v("46.Zelei Liu (Nanyang Technological University) "),_c('br'),_vm._v("47.Zichen Chen (Nanyang Technological University) "),_c('br'),_vm._v("48.Yuanyuan Chen (Nanyang Technological University) "),_c('br'),_vm._v("49.Zheng Xu (University of Science and Technology of China) "),_c('br'),_vm._v("50.Zhengfei Li (E Funds) ")])])]),_c('h1',[_vm._v("CVs of Organizing Committee")]),_c('div',{staticClass:"cente-text mg-bottom"},[_c('div',{staticClass:"cente-text-cente justify"},[_c('span',[_vm._v("Shuoling Liu ")]),_vm._v(" is the Managing Director, Head of Institute of Innovation, and Executive Chairman of FinTech Technology Committee at E Fund. He is also a FinTech Committee member of Asset Management Association of China (AMAC) and a member of Quantitative Investment Committee of Investment Technology League (ITL). His research interest includes algorithmic trading, artificial-Intelligence based bond default prediction, and big-data-based ESG research. His research on bond default was awarded the second prize of the Annual FinTech Development by the People’s Bank of China in 2020 and selected as the Capital Market FinTech Innovation Pilot Project in Guangzhou. He holds more than 10 FinTech-related patents. "),_c('br'),_vm._v("Email: liushuoling@efunds.com.cn"),_c('br'),_c('br'),_c('span',[_vm._v("Xueyang Wu")]),_vm._v(" received his PhD from the Hong Kong University of Science and Technology in 2023. He is the co-founder of Flaiverse and is currently principal scientist at Flaiverse. Xueyang Wu has been involved in the development of numerous industrial AI applications that highly related to LLMs. His research interests are transfer learning, federated learning, speech and natural language processing, and their instrumental improvement for LLMs. He published many research papers and patents. His professional services include serving as a reviewer for some of the most prestigious conferences in the field of Artificial Intelligence, such as NeurIPS, KDD, ICML, ICLR, and many others. "),_c('br'),_vm._v("Email: xwuba@connect.ust.hk"),_c('br'),_c('br'),_c('span',[_vm._v("Yongpeng Tang ")]),_vm._v(" is a highly senior FinTech expert in mutual fund industry with experience of over 20 years. He works at E Fund as Managing Director and Head of Application Development of E Fund. He has been involved in the development of numerous innovative products and services and acquired more than 10 FinTech-related patents and was awarded the second prize of the Annual FinTech Development by the People’s Bank of China in 2020. He sets up a scholarship at the Institute of Computing Science of the Chinese Academy of Sciences to encourage students to join the financial technology industry. He has a strong domestic and international presence and has been invited to attend artificial intelligence and financial forums and conferences for countless times and delivered keynote speeches. "),_c('br'),_vm._v("Email: typ@efunds.com.cn"),_c('br'),_c('br'),_c('span',[_vm._v("Qian Xu")]),_vm._v(" received her PhD from Hong Kong University of Science and Technology in 2011. She is currently an Adjunct Associate Professor of Artificial Intelligence Thrust at HKUST(GZ). Her research interests include Artificial Intelligence, Transfer Learning, Federated Learning, Natural Language Processing, with a particular focus on LLMs. She has extensive experience and expertise in the field of LLMs, having published many research papers and patents in this area. She has been serving for several High-tech companies for many years, like Baidu Inc. and Webank Inc. Her research results have been applied to search engine, recommendation system and digital human. These well-known Internet products are used by billions of users. She is Executive Director of Guangdong Society of Artificial Intelligence and Robotics, and one of founding members of Guangdong-Hong Kong-Macao Greater Bay Area Society of Artificial Intelligence and Robotics. "),_c('br'),_vm._v("Email: qianxu@ust.hk"),_c('br'),_c('br'),_c('span',[_vm._v("Liyuan Chen")]),_vm._v(" is Chief Operating Officer and Chairman of Fin-Tech Technology Committee of E Fund. She is a senior executive with more than 10 years of experience in business and technology. She has been committed to promoting the application of financial technology and made great contributions of the digital intelligence level of E Fund as well as the industry. She leads the FinTech Sector of E Fund, whose scientific researches has been transformed into high-profit productivities and publications in top-tier conferences such as IJCAI."),_c('br'),_vm._v(" Email: chenly@efunds.com.cn"),_c('br'),_c('br'),_c('span',[_vm._v("Qiang Yang")]),_vm._v(" received his PhD from Computer Science Department of the University of Maryland, College Park in 1989. He had been an assistant/associate professor at the University of Waterloo between 1989 and 1995, and a professor and NSERC Industrial Research Chair at Simon Fraser University in Canada from 1995 to 2003. He had been an associate professor at Department of Computer Science and Engineering (CSE), The Hong Kong University of Science and Technology (HKUST) from 2001 to 2007, and a full professor in the same department since 2007. He was the founding head of No-ah's Ark Lab (2012-2015). He is the Department Head of CSE, HKUST and University New Bright Professor of Engineering and Chair Professor since 2015. He is a Fellow of AAAI, ACM, IEEE, AAAS, IAPR, and CAAI. His professional services include IJCAI President (2017-2019), ACM SIGART (SIGAI) Vice Chair (2009-2012); PC Chair or Co-Chair of IJCAI (2015), ACM KDD (2010); Conference Chair of ACM KDD (2012), ACM RecSys (2013), ACM IUI (2010), etc; 2017 IJCAI Awards Committee Chair; 2017 ACM SIGKDD Test of Time Award Chair; 2017 IEEE Intelligent Systems “AI Ten to Watch” Chair; 2018 ACM SIGKDD Award Chair; Editor in Chief of IEEE Transactions on BIG DATA (2015-present) and ACM Trans. on Intelligent Systems and Technology (2009-2014); Associate Editor of IEEE Intelligent Systems (2005-present) and IEEE Transactions on Knowledge and Data Engineering (2005-2009). "),_c('br'),_vm._v("Email: qyang@cse.ust.hk"),_c('br'),_vm._v(" Google Scholar: "),_c('br'),_vm._v("https://scholar.google.com/citations?user=1LxWZLQAAAAJ ")])]),_c('h1',[_vm._v("List of 5 key publications on Large Language Models for Financial Services:")]),_c('div',{staticClass:"cente-text mg-bottom"},[_c('div',{staticClass:"cente-text-cente justify"},[_vm._v(" 1.Aohan Zeng, Xiao Liu, Zhengxiao Du, Zihan Wang, Hanyu Lai, Ming Ding, Zhuoyi Yang, Yifan Xu, Wendi Zheng, Xiao Xia, Weng Lam Tam, Zixuan Ma, Yufei Xue, Jidong Zhai, Wenguang Chen, Zhiyuan Liu, Peng Zhang, Yuxiao Dong, and Jie Tang. GLM-130B: An Open Bilingual Pre-trained Model. In ICLR 2023. "),_c('br'),_vm._v("2.Ming Ding, Wendi Zheng, Wenyi Hong, and Jie Tang. CogView2: Faster and Better Text-to-Image Generation via Hierarchical Transformers. In NeurIPS 2022. "),_c('br'),_vm._v("3.Zhengxiao Du, Yujie Qian, Xiao Liu, Ming Ding, Jiezhong Qiu, Zhilin Yang, and Jie Tang. GLM: General Language Model Pretraining with Autoregressive Blank Infilling. In ACL 2022. "),_c('br'),_vm._v("4.Shijie Wu, Ozan Irsoy, Steven Lu, Vadim Dabravolski, Mark Dredze, Sebastian Gehrmann, Prabhanjan Kambadur, David Rosenberg, Gideon Mann. BloombergGPT: A Large Language Model for Finance. 2023 https://doi.org/10.48550/arXiv.2303.17564 Liu Z , Huang D , Huang K , et al. FinBERT: A Pre-trained Financial Language Representation Model for Financial Text Mining. In IJCAI 2020. ")])])]),_c('div',{staticClass:"footer"})])}]

